/* 
---------------------------------------------
header
--------------------------------------------- 
*/

.background-header {
  background-color: #fff;
  height: 80px !important;
  position: fixed !important;
  top: 0px;
  left: 0px;
  right: 0px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15) !important;
}

.background-header .logo,
.background-header .main-nav .nav li a {
  color: #1e1e1e !important;
}

.background-header .main-nav .nav > li:hover > a {
  color: #f55858 !important;
}

.background-header .main-nav .nav > li > ul > li:hover > a {
  color: #f55858 !important;
}

.background-header .nav li a.active {
  color: #f55858 !important;
}

.header-areas {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 100;
  height: 100px;
  transition: all 0.5s ease 0s;
}

.header-areas .main-nav {
  min-height: 80px;
  background-color: #fff;
}

 .header-areas {


   box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.1);
}

.header-areas .main-nav .logo {
  line-height: 80px;
 color: #555;
  font-size: 28px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  float: left;
  transition: all 0.3s ease 0s;
}

.header-areas .main-nav .nav {
  float: right;
  margin-top: 27px;
  margin-left: 20px;
  margin-right: 0px;
  transition: all 0.3s ease 0s;
  position: relative;
  z-index: 999;
}

.header-areas .main-nav .nav li {
  padding-left: 20px;
  padding-right: 20px;
}

.header-areas .main-nav .nav li:last-child {
  padding-right: 0px;
}

.header-areas .main-nav .nav li a {
  display: block;
  font-weight: 500;
  font-size: 13px;
  color: #555;
  text-transform: uppercase;
  transition: all 0.3s ease 0s;
  height: 40px;
  line-height: 40px;
  border: transparent;
  letter-spacing: 1px;

}

.header-areas .main-nav .nav li:hover a {
  color: #f55858;
}

.header-areas .main-nav .nav li.submenu {
  position: relative;
  padding-right: 35px;
}

.header-areas .main-nav .nav li.submenu:after {
  font-family: FontAwesome;
  content: "\f107";
  font-size: 12px;
  color: #fff;
  position: absolute;
  right: 18px;
  top: 12px;
}

.header-areas.background-header .main-nav .nav li.submenu:after {
  color: #000;
}

.header-areas .main-nav .nav li.submenu ul {
  position: absolute;
  width: 200px;
  box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
  top: 40px;
  opacity: 0;
  transform: translateY(-2em);
  visibility: hidden;
  z-index: -1;
  transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s,
    z-index 0s linear 0.01s;
}

.header-areas .main-nav .nav li.submenu ul li {
  margin-left: 0px;
  padding-left: 0px;
  padding-right: 0px;

}

.header-areas .main-nav .nav li.submenu ul li a {
  display: block;
  background: #fff;
  color: #3b566e;
  padding-left: 20px;
  height: 40px;
  line-height: 40px;
  transition: all 0.3s ease 0s;
  position: relative;
  font-size: 13px;
  border-bottom: 1px solid #f5f5f5;
}

.header-areas .main-nav .nav li.submenu ul li a:before {
  content: "";
  position: absolute;
  width: 0px;
  height: 40px;
  left: 0px;
  top: 0px;
  bottom: 0px;
  transition: all 0.3s ease 0s;
  background: #f55858;
}

.header-areas .main-nav .nav li.submenu ul li a:hover {
  background: #fff;
  color: #f55858;
  padding-left: 25px;
}

.header-areas .main-nav .nav li.submenu ul li a:hover:before {
  width: 3px;
}

.header-areas .main-nav .nav li.submenu:hover ul {
  visibility: visible;
  opacity: 1;
  z-index: 1;
  transform: translateY(0%);
  transition-delay: 0s, 0s, 0.3s;
}

.header-areas .main-nav .menu-trigger {
  cursor: pointer;
  display: block;
  position: absolute;
  top: 23px;
  width: 32px;
  height: 40px;
  text-indent: -9999em;
  z-index: 99;
  right: 40px;
  display: none;
}

.header-areas .main-nav .menu-trigger span,
.header-areas .main-nav .menu-trigger span:before,
.header-areas .main-nav .menu-trigger span:after {
  transition: all 0.4s;
  background-color: #fff;
  display: block;
  position: absolute;
  width: 30px;
  height: 2px;
  left: 0;
}

.background-headers .main-nav .menu-trigger span,
.background-headers .main-nav .menu-trigger span:before,
.background-headers .main-nav .menu-trigger span:after {
  background-color: #1e1e1e;
}

.header-areas .main-nav .menu-trigger span:before,
.header-areas .main-nav .menu-trigger span:after {
  transition: all 0.4s;
  background-color: #fff;
  display: block;
  position: absolute;
  width: 30px;
  height: 2px;
  left: 0;
  width: 75%;
}

.background-headers .main-nav .menu-trigger span:before,
.background-headers .main-nav .menu-trigger span:after {
  background-color: #1e1e1e;
}

.header-areas .main-nav .menu-trigger span:before,
.header-areas .main-nav .menu-trigger span:after {
  content: "";
}

.header-areas .main-nav .menu-trigger span {
  top: 16px;
}

.header-areas .main-nav .menu-trigger span:before {
  transform-origin: 33% 100%;
  top: -10px;
  z-index: 10;
}

.header-areas .main-nav .menu-trigger span:after {
  transform-origin: 33% 0;
  top: 10px;
}

.header-areas .main-nav .menu-trigger.active span,
.header-areas .main-nav .menu-trigger.active span:before,
.header-areas .main-nav .menu-trigger.active span:after {
  background-color:#fff;
  width: 100%;
}

.header-areas .main-nav .menu-trigger.active span:before {
  transform: translateY(6px) translateX(1px) rotate(45deg);
  background-color: #fff;
}

.background-header .main-nav .menu-trigger.active span:before {
  background-color: #1e1e1e;
}

.header-areas .main-nav .menu-trigger.active span:after {
  transform: translateY(-6px) translateX(1px) rotate(-45deg);
  background-color: #fff;
}

.background-headers .main-nav .menu-trigger.active span:after {
  background-color: #1e1e1e;
}

